.pricing {
  z-index: 10;
  padding: 4rem 0;
  display: flex;
  justify-content: center;
  box-shadow: 0px -10px 30px rgba(0, 0, 0, 0.3);

  .pricingContainer {
    text-align: left;
    width: 90%;
    max-width: 1500px;
    padding: 0 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;

    h2 {
      font-size: 1.5rem;
    }

    .pricingTable {
      padding: 0.5rem;
      font-size: 1.25rem;
      font-weight: lighter;
      text-align: left;
      border-width: 2px;
      border-collapse: separate;
      border-spacing: 1px;
      border-color: #43464b;
      border-radius: 0.5rem;
      background-color: #f5f5f5;
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);

      th {
        padding: 1.5rem;
      }
      td {
        padding: 1.5rem;
        background-color: #dcdcdc;
      }
    }

    .downTable {
      font-size: 1rem;
      margin-top: 1rem;
    }

    .additionally {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 1.5rem;

      h3 {
        font-size: 1.125rem;
      }

      &.points {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 1;
      }
    }

    p {
      font-size: 1rem;
    }

    .downTable {
      font-size: 1rem;
    }
  }

  @media (max-width: 1200px) {
    .pricingContainer {
      .pricingTable {
        font-size: 1rem;
      }
    }
  }

  @media (max-width: 768px) {
    .pricingContainer {
      h2 {
        font-size: 1.3rem;
      }

      .pricingTable {
        font-size: 0.8rem;

        th {
          padding: 0.5rem;
        }
        td {
          padding: 0.5rem;
        }

        .hiddenForMobile {
          display: none;
        }
      }

      p {
        font-size: 0.8rem;
      }

      .downTable {
        font-size: 0.8rem;
      }

      .additionally {
        h3 {
          font-size: 0.9rem;
        }
      }
    }
  }
}
