.previewGallery {
  width: 1045px;
  overflow: hidden;
  position: relative;

  &Track {
    position: relative;
    left: 400px;
    display: flex;
    white-space: nowrap;
    transition: transform 0.5s ease-out;
  }

  &Preview {
    min-width: 154px;
    margin: 5px;
    display: inline-block;
    height: 80px;
    background: #97a9b2;
    border-radius: 5px;
    overflow: hidden;
    padding: 0;
    border: none;
    cursor: pointer;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }
  }

  &Image {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &Cover {
    height: 100%;
    position: absolute;
    z-index: 2;
    width: 164px;
    background: rgba(255, 255, 255, 0.7);
    left: 50%;
    top: 0;
    transform: translate(-50%, 0);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font: 24px/1 Georgia;
  }

  @media (max-width: 1200px) {
    width: 900px;

    &Track {
      left: 325px;
    }
  }

  @media (max-width: 1024px) {
    width: 700px;

    &Track {
      left: 225px;
    }
  }
}

@media only screen and (max-width: 768px) {
  .previewGallery {
    display: none;
  }
}
